/* eslint no-useless-escape: 0 */
const CHARS = 'abcdefghigklmnopqrstuvwxyz';
const NUMS = '0123456789';
const ALL = CHARS + NUMS;

const generateKey = (node?) => {
  let repeatStr = '';
  if (node) {
    const texts = node.getTexts() || [];
    const string = texts.map(t => t.text).join('');
    if (string) {
      repeatStr = `${string
        .toLowerCase()
        .replace(/[\!|\#|\s|\r|\n|\<|\>|\/|\"|\']+/g, '-')}-`;
    }
  }

  for (let i = 0; i < 4; i += 1) {
    repeatStr += 'z';
  }

  return `${repeatStr}xx`.replace(/[xz]/g, c =>
    c === 'x' ? CHARS[(Math.random() * 26) | 0] : ALL[(Math.random() * 36) | 0],
  );
};

export default generateKey;
